.submit-btn {
  padding: 0px 15px;
  height: 40px;
  border-radius: 3px;
  background-color: #007dfe;
  color: #ffffff;
  font-family: TextBook, sans-serif;
  font-weight: normal;
  border: none;
  text-align: center;
  font-size: 16px;
  cursor: pointer;
  outline: none;
  transition: .15s ease-in-out;
  &:hover, &:focus {
    background-color: #0059ff
  };

  &:disabled {
    background-color: #e1e5e8;
    color: #c8cbcf
  };

  &:active {
    background-color: #0011fd;
  };

  @media(max-width: 540px) {
    padding: 0px 20px;
  }
}

.submit-btn--round {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 35px;
  border-radius: 100%;
  @media(max-width: 540px) {
    padding: 0px;
  }
}