@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loading {
  font-size: 10px;
  border-radius: 50%;
  margin: 0px auto;
  position: relative;
  width: 10em;
  height: 10em;
  text-align: center;
  transform: translateZ(0);
  border-top: 1.1em solid rgba(0, 89, 255, 0.1);
  border-right: 1.1em solid rgba(0, 89, 255, 0.1);
  border-bottom: 1.1em solid rgba(0, 89, 255, 0.1);
  border-left: 1.1em solid #0059ff;
  animation: spin 0.9s infinite linear;
  &::before, &::after {
    width: 10em;
    height: 10em;
    border-radius: 50%;
  }
}

.loading__wrapper {
  position: absolute;
  z-index: 1000;
  width: 100%;
  height: 100vh;
  background-color: rgba(140, 152, 165, 0.5);
  display: flex;
  align-items: center
}